.socials-list {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  max-width: 112px;

  @include vp-1023 {
    max-width: 95px;
  }

  @include vp-767 {
    margin-left: -5px;
  }
}

.socials-list__item {
  margin-right: 7px;

  @include vp-1023 {
    margin-right: 5px;
  }

  a {
    display: flex;
    padding: 5px;

    @include vp-1023 {
      svg {
        width: 16px;
        height: 16px;
      }
    }

    @include hover-focus {
      opacity: 0.8;

      &:focus {
        outline: 2px solid #f9fbfd;
      }
    }
  }
}
