.product-card {
  min-height: 386px;
  position: relative;

  @include vp-1023 {
    height: calc((100vw - 120px) / 2 * 386 / 324);
  }

  @include vp-767 {
    width: calc(100vw - 50px);
    height: calc((100vw - 50px) * 386 / 270);
    min-width: 270px;
  }

  h3 {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    margin: 0;
  }

  &:focus-within {
    .product-card__front {
      transform: rotateY(-180deg);
      transition: $trans-600;
    }

    .product-card__back {
      transform: rotateY(0deg);
      transition: $trans-600;
    }
  }

  &:hover {
    .product-card__front {
      transform: rotateY(-180deg);
      transition: $trans-600;
    }

    .product-card__back {
      transform: rotateY(0deg);
      transition: $trans-600;
    }

    // &:focus {
    //   .product-card__front {
    //     transform: rotateY(-180deg);
    //     transition: $trans-600;
    //   }

    //   .product-card__back {
    //     transform: rotateY(0deg);
    //     transition: $trans-600;
    //   }
    // }
  }

  // &:focus {
  //   .product-card__front {
  //     transform: rotateY(-180deg);
  //     transition: $trans-600;
  //   }

  //   .product-card__back {
  //     transform: rotateY(0deg);
  //     transition: $trans-600;
  //   }
  // }
}

.product-card__front {
  width: 100%;
  height: 100%;
  transform: rotateY(0deg);
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  padding: 22px 29px;

  @include vp-1023 {
    padding: 26px 35px;
  }

  @include vp-767 {
    padding: 16px 24px;
  }

  h3 {
    margin-top: auto;
  }

  &--astoria {
    background-image: url("../img/cruises/cruises-content-1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include retina {
      background-image: url("../img/cruises/cruises-content-1@2x.jpg");
    }
  }

  &--elina {
    background-image: url("../img/cruises/cruises-content-2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include retina {
      background-image: url("../img/cruises/cruises-content-2@2x.jpg");
    }
  }

  &--atlantis {
    background-image: url("../img/cruises/cruises-content-3.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include retina {
      background-image: url("../img/cruises/cruises-content-3@2x.jpg");
    }
  }

  &--odysseus {
    background-image: url("../img/cruises/cruises-content-4.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include retina {
      background-image: url("../img/cruises/cruises-content-4@2x.jpg");
    }
  }

  &--olympia {
    background-image: url("../img/cruises/cruises-content-5.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include retina {
      background-image: url("../img/cruises/cruises-content-5@2x.jpg");
    }
  }

  &--aurora {
    background-image: url("../img/cruises/cruises-content-6.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include retina {
      background-image: url("../img/cruises/cruises-content-6@2x.jpg");
    }
  }

  &--leonor {
    background-image: url("../img/cruises/cruises-content-7.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include retina {
      background-image: url("../img/cruises/cruises-content-7@2x.jpg");
    }
  }

  &--minio {
    background-image: url("../img/cruises/cruises-content-8.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include retina {
      background-image: url("../img/cruises/cruises-content-8@2x.jpg");
    }
  }
}

.product-card__back {
  background-color: #011c40;
  width: 100%;
  height: 100%;
  transform: rotateY(180deg);
  backface-visibility: hidden;
  padding: 29px 25px 11px 25px;
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: auto;

  @include vp-1023 {
    padding: 31px 34px 11px;
  }

  @include vp-767 {
    padding: 30px 25px 11px;
  }

  h3 {
    margin-bottom: 13px;
  }
}

.product-card__info {
  display: flex;
  flex-direction: column;
  margin: 0;

  dd + dt {
    margin-top: 12px;
  }

  dt {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
  }

  dd {
    font-size: 14px;
    line-height: 20px;
    color: #d8d8d9;
    margin: 0;
  }
}

.product-card__button {
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: $color-alice-blue;
  align-items: center;
  justify-content: center;
  margin-top: auto;
  transition: unset;
  user-select: none;

  @include hover-focus {
    opacity: 0.8;

    &:focus {
      outline: 2px solid #f9fbfd;
    }
  }
}
